import { Vnode } from 'mithril';
import { isInputField } from '../models/input-field';
import { Form } from '../models/form';
import { inputFieldFactory } from './input-field-factory';

export const formFactory = <T>(form: Form<T>, result = {} as T, onchange?: () => void): Array<Vnode<any, any>> => {
  return Object.keys(form).reduce((acc, k) => {
    const key = k as Extract<keyof Partial<T>, string>;
    const fc = form[key];
    // const onchange = () => console.log('form changed');
    if (isInputField(fc)) {
      return [...acc, inputFieldFactory(key, fc, result, { onchange })];
    } else {
      return [...acc, ...formFactory(fc as Form<T>, result, onchange)];
    }
  }, [] as Array<Vnode<any, any>>);
};
